:host {
  display: block;
}

.inline-vehicle-selector {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.5rem;
  background-color: var(--card-background-color, #f8fafc);
  border-radius: 0.5rem;
  border: 1px solid var(--input-border-color, #e2e8f0);
}

// Header
.selector-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.search-box {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex: 1;
  min-width: 150px;
  padding: 0.375rem 0.625rem;
  background-color: var(--input-background-color, #ffffff);
  border: 1px solid var(--input-border-color, #d1d5db);
  border-radius: 0.375rem;

  i {
    color: var(--text-color-secondary, #9ca3af);
    font-size: 0.875rem;
  }
}

.search-input {
  flex: 1;
  border: none;
  outline: none;
  font-size: 0.8125rem;
  background: transparent;
  color: var(--text-color, #374151);

  &::placeholder {
    color: var(--text-color-secondary, #9ca3af);
  }
}

.header-actions {
  display: flex;
  gap: 0.375rem;
}

.action-btn {
  padding: 0.375rem 0.625rem;
  font-size: 0.75rem;
  font-weight: 500;
  background: transparent;
  border: 1px solid var(--input-border-color, #d1d5db);
  border-radius: 0.375rem;
  color: var(--text-color, #4b5563);
  cursor: pointer;
  transition: all 0.15s ease;

  &:hover {
    background-color: var(--surface-hover, #f3f4f6);
    border-color: var(--text-color-secondary, #9ca3af);
  }
}

// Vehicle List
.vehicle-list {
  display: flex;
  flex-direction: column;
  gap: 0.375rem;
  max-height: 200px;
  overflow-y: auto;
  padding: 0.25rem;
}

.vehicle-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.625rem;
  background-color: var(--input-background-color, #ffffff);
  border: 1.5px solid transparent;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.15s ease;

  &:hover {
    background-color: var(--surface-hover, #f0f2f5);
  }

  &.selected {
    background-color: rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
    border-color: var(--color-primary-color, #3b82f6);
  }

  input[type='checkbox'] {
    width: 1rem;
    height: 1rem;
    accent-color: var(--color-primary-color, #3b82f6);
    cursor: pointer;
    flex-shrink: 0;
  }
}

.vehicle-info {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-width: 0;
}

.vehicle-name {
  font-size: 0.8125rem;
  font-weight: 500;
  color: var(--text-color, #1e293b);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.vehicle-reg {
  font-size: 0.6875rem;
  color: var(--text-color-secondary, #64748b);
}

.selected-icon {
  color: var(--color-primary-color, #3b82f6);
  font-size: 0.875rem;
  flex-shrink: 0;
}

// Empty State
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  color: var(--text-color-secondary, #9ca3af);

  i {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }

  p {
    margin: 0;
    font-size: 0.8125rem;
  }
}

// Selection Summary
.selection-summary {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.75rem;
  padding: 0.5rem 0.75rem;
  background-color: rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
  border-radius: 0.375rem;
  border: 1px solid var(--color-primary-color, #3b82f6);
}

.summary-text {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.8125rem;
  font-weight: 500;
  color: var(--color-primary-color, #3b82f6);

  i {
    font-size: 1rem;
  }
}

::ng-deep .confirm-btn {
  background: var(--color-primary-color, #3b82f6);
  border: none;
  font-weight: 600;

  &:hover {
    filter: brightness(0.9);
  }
}
